<template>
  <div class="pagination-content">
    <el-pagination
      @size-change="onHandleSizeChange"
      @current-change="onHandleCurrentChange"
      background
      :page-size="pageSize"
      :total="total"
      :current-page="currentPage"
      :layout="layout"
      :page-sizes="[100, 200, 300, 400]"
      prev-text="上一页"
      next-text="下一页"
      hide-on-single-page
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  props: {
    pageSize: {
      type: Number,
      default: 100
    },
    total: {
      type: Number,
      default: 400
    },
    currentPage: {
      type: Number,
      default: 4
    },
    layout: {
      type: String,
      default: "total, sizes, jumper, next, pager, prev"
    }
  },
  methods: {
    onHandleSizeChange(val) {
      this.$emit("handleSizeChange", val);
    },
    onHandleCurrentChange(val) {
      this.$emit("handleCurrentChange", val);
    }
  }
};
</script>
