<template>
  <el-table
    @selection-change="onHandleSelectionChange"
    :data="tableData"
    border
  >
    <slot name="tableLeft" />
    <af-table-column
      v-for="item in tableField"
      :key="item.prop"
      :label="item.label"
      :prop="item.prop"
      align="center"
    />
    <slot name="operation" />
  </el-table>
</template>

<script>
export default {
  props: {
    tableData: {
      type: Array,
      default: []
    },
    tableField: {
      type: Array,
      default: []
    }
  },
  methods: {
    onHandleSelectionChange(val) {
      this.$emit("handleSelectionChange", val);
    }
  }
};
</script>

